body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#map {
  position: absolute;
  width: 100%;
  height: 100%;
}

.markerlist {
  list-style: none;
  border-radius: 18px;
  width: 130px;
  padding: 20px 20px 20px 40px;
}

.markerlist-container {
  position: absolute;
  right: 20px;
}

.markerlist-btn, .markerlist-container ul {
  box-shadow: 0 0 10px 2px rgba(31,51,73,.1);
  background-color: #fff;
  border-color: #fff;
}

.export-btn {
  height: 25px;
  width: 190px;
  border-radius: 9px;
  outline: none;
  cursor: pointer;
}

.markerlist li {
  padding-bottom: 5px;
}

.score-title,
.score-value {
  display: inline-block;
  width: 50%;
}

.score-value {
  text-align: left;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.marker-color-item {
  display: inline-block;
  width: 35px;
  height: 35px;
  margin-right: 5px;
  color: white;
  text-align: center;
  vertical-align: middle;
  line-height: 35px;
  cursor: pointer;
}

.mapboxgl-marker.mapboxgl-marker-anchor-center, .remove-button {
  cursor: pointer;
}

.marker-color-container, .remove-button-container {
  float: left;
}

.remove-button-container {
  float: right;
  padding-top: 5px;
  padding-left: 5px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

